import { useEffect } from 'react';
import Header from '../../components/feature/Header';
import Footer from '../../components/feature/Footer';
import { useTermsAndConditions } from '../../hooks';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default function TermsAndConditionsPage() {
  const { content, loading } = useTermsAndConditions();

  useEffect(() => {
    window.scrollTo(0, 0);
    AOS.init({
      duration: 800,
      once: true,
      easing: 'ease-out-cubic'
    });
  }, []);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[#7ED957]"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      <div className="pt-20">
        <Header />
      </div>

      {/* Content Section */}
      <section className="py-16">
        <div className="max-w-4xl mx-auto px-6">
          {/* Page Title */}
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-6 text-center" data-aos="fade-down">
            {content?.title || 'Terms and Conditions'}
          </h1>
          {content?.subtitle && (
            <p className="text-xl text-gray-600 mb-8 text-center" data-aos="fade-up" data-aos-delay="100">
              {content.subtitle}
            </p>
          )}
          <div className="prose prose-lg max-w-none">
            {content?.richTextContent ? (
              <div 
                className="rich-text-content text-gray-700 leading-relaxed"
                dangerouslySetInnerHTML={{ __html: content.richTextContent }}
                data-aos="fade-up"
                style={{
                  whiteSpace: 'pre-wrap',
                  wordWrap: 'break-word'
                }}
              />
            ) : (
              <div className="space-y-6 text-gray-700">
                <p className="text-base leading-relaxed text-gray-500 italic">
                  Content will be managed through the CMS. Please add content using the admin dashboard.
                </p>
              </div>
            )}
          </div>
          
          <style>{`
            .rich-text-content {
              line-height: 1.8;
              color: #374151;
            }
            .rich-text-content h1,
            .rich-text-content h2,
            .rich-text-content h3,
            .rich-text-content h4,
            .rich-text-content h5,
            .rich-text-content h6 {
              font-weight: bold;
              margin-top: 1.5em;
              margin-bottom: 0.75em;
              color: #1f2937;
              line-height: 1.3;
            }
            .rich-text-content h1 { 
              font-size: 2em; 
              font-weight: 700;
            }
            .rich-text-content h2 { 
              font-size: 1.75em; 
              font-weight: 700;
            }
            .rich-text-content h3 { 
              font-size: 1.5em; 
              font-weight: 700;
            }
            .rich-text-content h4 { 
              font-size: 1.25em; 
              font-weight: 700;
            }
            .rich-text-content p {
              margin-bottom: 1em;
              line-height: 1.8;
              color: #374151;
            }
            .rich-text-content p:last-child {
              margin-bottom: 0;
            }
            .rich-text-content strong,
            .rich-text-content b {
              font-weight: 700;
              color: #1f2937;
            }
            .rich-text-content em,
            .rich-text-content i {
              font-style: italic;
            }
            .rich-text-content ul,
            .rich-text-content ol {
              margin-left: 1.5em;
              margin-bottom: 1em;
              padding-left: 1.5em;
            }
            .rich-text-content li {
              margin-bottom: 0.5em;
              line-height: 1.8;
            }
            .rich-text-content br {
              line-height: 1.8;
            }
            .rich-text-content div {
              margin-bottom: 1em;
              line-height: 1.8;
            }
            .rich-text-content * {
              max-width: 100%;
            }
            .rich-text-content [style*="text-align: center"] {
              text-align: center !important;
            }
            .rich-text-content [style*="text-align: right"] {
              text-align: right !important;
            }
            .rich-text-content [style*="text-align: left"] {
              text-align: left !important;
            }
          `}</style>
        </div>
      </section>

      <Footer />
    </div>
  );
}

